import circle from '../public/addCircle.png';
import search from '../public/searchNormal.png';
import CampaignTable from './campaignTable';
import {Link} from 'react-router-dom';
import {useState,useEffect} from 'react';
import { getCampaigns } from '../api';

const styles = {
    top: {
        display: 'flex',
        width: '100%',
        alignItems: 'center',
        justifyContent: 'space-between'
    },
    container: {
        margin: 25
    },
    campaign_button: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: 237,
        height: 50,
        backgroundColor: '#0F6EFF',
        border: 'none',
        borderRadius: 10,
        color: 'white',
        fontSize: 15,
        cursor: 'pointer',
        textDecoration:'none',

    },
    campaign: {
        marginTop: 15,
        height: 450,
        backgroundColor: 'white',
        borderRadius: 10,
        boxShadow: '0px 0px 1px 1px #dae5ff'
    },
    filtercol: {
        padding: 25,
        width: '100%',
        display: 'flex',
        justifyContent:'space-between'

    },
    search: {
        height: 50,
        width: 315,
        borderRadius: 10,
        backgroundImage: `url(${search})`,
        backgroundRepeat: 'no-repeat',
        backgroundPosition: '10px 10px',
        padding: '0px 0px 0px 40px',
        border: '3px solid #f7f7f7',
    },
    filter: {
        height: 50,
        borderRadius: 10,
        backgroundPosition: '10px 10px',
        border: '3px solid #f7f7f7',
        marginRight:20,
        fontWeight:700
    }

}

const initialState={
    title:"",
    platform:"All Platforms",
    status:"All Status",
    date:"Last 30 days"
}

const Campaign = () => {
    const [filter,setFilter]=useState(initialState);
    const [Data,setData]=useState([]);

    const handleChange=(e)=>{
        setFilter({...filter,[e.target.name]:e.target.value});
    }

    const get_data=async()=>{
        setTimeout(async()=>{
            const res=await getCampaigns(filter);
            return setData(res.data);
        },10)
    }


    useEffect(()=>{
        get_data();
    },[filter]);

    console.log(Data);

    return (<>
        <div className="continer" style={styles.container}>
            <div style={styles.top}>
                <div>
                    <h1>Your Campigns</h1>
                    <p style={{ color: 'gray' }}>Check the list of campigns you created</p>
                </div>
                <Link to='/create' style={styles.campaign_button}><img style={{ width: 30, marginTop: 5, marginRight: 5 }} src={circle}></img>Create new Campaign</Link>
            </div>
            <div className="Campaigns" style={styles.campaign}>
                <div style={styles.filtercol}>
                    <input style={styles.search} placeHolder="Search for the campaign" name="title" value={filter.title} onChange={(e)=>handleChange(e)}></input>
                    <div>
                        <label style={{color:'gray'}} >Platform:&nbsp;&nbsp;</label>
                        <select style={styles.filter} name="platform" value={filter.platform} onChange={(e)=>handleChange(e)}>
                            <option value="All Platforms">All Platforms</option>
                            <option value="FaceBook">FaceBook</option>
                            <option value="Google">Google</option>
                            <option value="Youtube">Youtube</option>
                            <option value="Instagram">Instagram</option>
                        </select>
                        
                        <label style={{color:'gray'}} >Status:&nbsp;&nbsp;</label>
                        <select style={styles.filter} name="status" value={filter.status} onChange={(e)=>handleChange(e)}>
                            <option value="All Status">All Status</option>
                            <option value="Live">Live</option>
                            <option value="Not Live">Not Live</option>
                        </select>

                        <select style={styles.filter} name="date" value={filter.date} onChange={(e)=>handleChange(e)}>
                            <option value="Last 30 days">Last 30 days</option>
                            <option value="Last 7 days">Last 7 days</option>
                            <option value="Last 24 Hours">Last 24 Hours</option>
                        </select>
                    </div>
                    
                </div>
                <CampaignTable get_data={get_data} Data={Data}></CampaignTable>
            </div>
        </div>
    </>)
}

export default Campaign;
