import Sidebar from './components/sidebar';
import { useState } from 'react';
import {
  BrowserRouter,
  Routes,
  Route,
} from "react-router-dom";
import Mainbar from './components/Mainbar';
import Form from './components/CreateCampaign';

function App() {
  const [curActive, setActive] = useState(1);

  return (
    <BrowserRouter>
        <div className="App" style={styles.app}>
          <Sidebar curActive={curActive} setActive={setActive}></Sidebar>
          <Routes>
          <Route path='/' element={<Mainbar curActive={curActive} />} />
          <Route path='/create' element={<Form/>} />
          </Routes>

        </div>
    </BrowserRouter>
  );
}

export default App;


const styles = {
  app: {
    display: 'flex',
    height: '100vh',
    width: '100vw',
    justifyContent: 'center',
    alignItems: 'center'
  },
}
