const Part1 = ({ formData,setFormData,setSteps }) => {
    
    const handleClick=(e)=>{
        setFormData({...formData,taskid:parseInt(e.currentTarget.id)});
    }

    return (<>
        <div style={styles.frame}>
            <p style={{ fontWeight: 1000, fontSize: 20 }}>What you want to do? <span style={{ color: 'gray', fontSize: 18, fontWeight: 400 }}>(step 1 of 4)</span></p>
            <br></br><hr></hr>
            <div style={styles.box}>
                {
                    part1_data.map((item) => <div onClick={(e)=>handleClick(e)} id={item.id} style={item.id!==formData.taskid? styles.card:styles.selectedcard}>
                        <div style={{ display: 'flex' }}>
                            <item.icon style={{ margin: 5 }} />
                            <div >
                                <p>{item.title}</p>
                                <p style={{ color: 'gray' }}>{item.description}</p>
                            </div>
                        </div>
                    </div>)
                }

            </div>
            <button style={styles.cntbtn} onClick={() => { setSteps(1) }}> Continue</button>
        </div>
    </>)
}

const Part2 = ({ formData,setFormData,setSteps }) => {

    const [products,setProducts]=useState([]);
    
    const getproducts=async()=>{
        const data=await getProducts();
        setProducts(data.data);
        setFormData({...formData,product:data.data[0]});
    }

    useEffect(()=>{
        getproducts();
    },[])


    const handleClick=(e)=>{
        setFormData({...formData,product:products[e.currentTarget.id]});
    }


    return (<>
        <div style={styles.frame}>
            <p style={{ fontWeight: 1000, fontSize: 20 }}>Choose the Product <span style={{ color: 'gray', fontSize: 18, fontWeight: 400 }}>(step 2 of 4)</span></p>
            <br></br><hr></hr>
            <div style={styles.box}>
                {
                    products.map((item,index) => <div onClick={(e)=>handleClick(e)} id={index} style={item!==formData.product? styles.card:styles.selectedcard}>
                        <div style={{ display: 'flex' }}>
                            <img src={item.icon} style={{ height: 40, margin: 5 }}></img>
                            <div >
                                <p>{item.title}</p>
                                <p style={{ color: 'gray' }}>INR: {item.description}</p>
                            </div>
                        </div>
                    </div>)
                }

            </div>
            <button style={styles.cntbtn} onClick={() => { setSteps(2) }}> Continue</button>
        </div>
    </>)
}

const Part3 = ({ formData, setFormData, setSteps }) => {

    const onToggle = (e) => {
        setFormData({ ...formData, [e.target.className]: 1 - formData[e.target.className] });
        console.log(formData);
    }

    const handleslider=(e)=>{
        setFormData({ ...formData, [e.target.name]: e.target.value});
    }

    const onChange=(e)=>{
        setFormData({...formData,[e.target.name]:e.target.value});
    }

    return (<>
        <div style={styles.formframe}>
            <p style={{ fontWeight: 1000, fontSize: 20 }}>Campaign Settings <span style={{ color: 'gray', fontSize: 18, fontWeight: 400 }}>(step 3 of 4)</span></p>
            <br></br><hr></hr>
            
            {/* Part 1 */}

            <div style={styles.formbox}>
                <div style={{ display: 'flex', margin: 10 }}>
                    <div style={{ height: 25, width: 25, backgroundColor: '#0f6dff', borderRadius: '50%', marginRight: 10, display: 'flex', justifyContent: 'center', alignItems: 'center', color: 'white' }}>1</div>
                    <p><b><u>Budget and dates info</u></b></p>
                </div>
                <div style={{ padding: 5, marginLeft: 20 }}>
                    <p>Budget and Timeline</p>
                    {
                        formData.timeline == 0 ?
                            <div style={{ width: 220, height: 39, backgroundColor: '#f0f0f0', borderRadius: 25, margin: 5, display: 'flex', cursor: 'pointer' }}>
                                <div className="timeline" style={{ width: 110, height: 39, backgroundColor: '#0f6dff', borderRadius: 25, color: 'white', display: 'flex', justifyContent: 'center', alignItems: 'center' }} onClick={(e) => onToggle(e)}>Lifetime</div>
                                <div className="timeline" style={{ width: 110, height: 39, backgroundColor: '#f0f0f0', borderRadius: 25, color: 'white', display: 'flex', justifyContent: 'center', alignItems: 'center' }} onClick={(e) => onToggle(e)}>Daily</div>
                            </div>
                            :
                            <div style={{ width: 220, height: 39, backgroundColor: '#f0f0f0', borderRadius: 25, margin: 5, display: 'flex' }}>
                                <div className="timeline" style={{ width: 110, height: 39, backgroundColor: '#f0f0f0', borderRadius: 25, color: 'white', display: 'flex', justifyContent: 'center', alignItems: 'center' }} onClick={(e) => onToggle(e)}>Lifetime</div>
                                <div className="timeline" style={{ width: 110, height: 39, backgroundColor: '#0f6dff', borderRadius: 25, color: 'white', display: 'flex', justifyContent: 'center', alignItems: 'center' }} onClick={(e) => onToggle(e)}>Daily</div>
                            </div>
                    }
                </div>
                <div style={{ margin: 20, width: '100%', display: 'flex', justifyContent: 'space-around' }}>
                    <TextField
                        id="date"
                        label="start date"
                        type="date"
                        name="startDate"
                        value={formData.startDate}
                        onChange={(e)=>onChange(e)}
                        sx={{ width: 400 }}
                        InputLabelProps={{
                            shrink: true,
                        }}
                    />

                    <TextField
                        id="date"
                        label="end date"
                        type="date"
                        name="endDate"
                        onChange={(e)=>onChange(e)}
                        value={formData.endDate}
                        sx={{ width: 400 }}
                        InputLabelProps={{
                            shrink: true,
                        }}
                    />
                </div>
                <div style={{ padding: 5, marginLeft: 20 }}>
                    <p>Enter Campaign Budget</p>
                    <div style={{display:'flex',width:'100%',justifyContent:'space-around'}}>
                    <Box sx={{ width: 300 }}>
                        <Slider
                            aria-label="Always visible"
                            defaultValue={formData.budget}
                            value={formData.budget}
                            step={1}
                            marks={marks}
                            name="budget"
                            onChange={(e)=>handleslider(e)}
                        />
                    </Box>
                    <h3>{formData.budget*1000}</h3>
                    </div>
                </div>
                
            </div>

            {/* Part 2 */}
            
            <div style={styles.formbox}>
            <div style={{ display: 'flex', margin: 10 }}>
                    <div style={{ height: 25, width: 25, backgroundColor: '#0f6dff', borderRadius: '50%', marginRight: 10, display: 'flex', justifyContent: 'center', alignItems: 'center', color: 'white' }}>2</div>
                    <p><b><u>Location Info</u></b></p>
                </div>
                <div style={{ padding: 5, marginLeft: 20 }}>
                    <p>Budget and Timeline</p>
                    {
                        formData.ltoggle == 0 ?
                            <div style={{ width: 220, height: 39, backgroundColor: '#f0f0f0', borderRadius: 25, margin: 5, display: 'flex', cursor: 'pointer' }}>
                                <div className="ltoggle" style={{ width: 110, height: 39, backgroundColor: '#0f6dff', borderRadius: 25, color: 'white', display: 'flex', justifyContent: 'center', alignItems: 'center' }} onClick={(e) => onToggle(e)}>Location</div>
                                <div className="ltoggle" style={{ width: 110, height: 39, backgroundColor: '#f0f0f0', borderRadius: 25, color: 'white', display: 'flex', justifyContent: 'center', alignItems: 'center' }} onClick={(e) => onToggle(e)}>Radius</div>
                            </div>
                            :
                            <div style={{ width: 220, height: 39, backgroundColor: '#f0f0f0', borderRadius: 25, margin: 5, display: 'flex' }}>
                                <div className="ltoggle" style={{ width: 110, height: 39, backgroundColor: '#f0f0f0', borderRadius: 25, color: 'white', display: 'flex', justifyContent: 'center', alignItems: 'center' }} onClick={(e) => onToggle(e)}>Location</div>
                                <div className="ltoggle" style={{ width: 110, height: 39, backgroundColor: '#0f6dff', borderRadius: 25, color: 'white', display: 'flex', justifyContent: 'center', alignItems: 'center' }} onClick={(e) => onToggle(e)}>Radius</div>
                            </div>
                    }
                <TextField style={{margin:20,width:700}} name="location" onChange={(e)=>onChange(e)} id="outlined-basic" label="Select Location" variant="outlined" />
                <div style={{ padding: 5, marginLeft: 20 }}>
                    <p>Radius</p>
                    <div style={{display:'flex',width:'100%',justifyContent:'space-around'}}>
                    <Box sx={{ width: 300 }}>
                        <Slider
                            aria-label="Always visible"
                            defaultValue={formData.radius}
                            value={formData.radius}
                            step={1}
                            marks={rmarks}
                            name="radius"
                            onChange={(e)=>handleslider(e)}
                        />
                    </Box>
                    <h3>{formData.radius*30/100}</h3>
                    </div>
                </div>
                </div>
                
            </div>
            <button style={styles.pt3_btn} onClick={() => { setSteps(3) }}> Continue</button>    
        </div>
        
    </>)
}


const Part4 = ({ formData,setFormData,setSteps }) => {

    const Navigate=useNavigate();
    const submit=async ()=>{
        const res=await postCampaign(formData);
        Navigate('/');
    }

    useState(()=>{
        setFormData({...formData,budget:formData.budget*1000});
    },[]);

    const handleClick=async (e)=>{
        await setFormData({...formData,outlookid:e.currentTarget.id});
    }

    return (<>
        <div style={styles.frame}>
            <p style={{ fontWeight: 1000, fontSize: 20 }}>Ready to go <span style={{ color: 'gray', fontSize: 18, fontWeight: 400 }}>(step 4 of 4)</span></p>
            <br></br><hr></hr>
            <div style={{display:'flex',justifyContent:'space-around'}}>
            {
                readytogo.map(item=>
                    <div onClick={(e)=>handleClick(e)} id={item.id} style={item.id!=formData.outlookid ? styles.final:styles.selectedfinal}>
                <div style={{display:'flex',justifyContent:'center'}}>
                <img  style={{width:52,marginRight:15,borderRadius:'50%'}} src="https://w7.pngwing.com/pngs/79/518/png-transparent-js-react-js-logo-react-react-native-logos-icon-thumbnail.png"></img>
                <div>
                <p style={{color:'#2a23a5',fontSize:15}}><b> Mukund Cake Shop</b></p>
                <p style={{color:'gray'}}>Sponsored</p>
                </div>
                </div>
                <p>We are best bakery around you. Please like my page to get updates on exciting offers and discounts.</p>
                <img src={item.img} style={{height:100,width:'100%',objectFit:'cover'}}></img>
                <div style={{display:'flex',justifyContent:'space-around',padding:5,alignItems:'center',backgroundColor:'#f5f5f5'}}>
                <p style={{color:'#2a23a5',fontSize:13}}><b> Mukund Cake Shop</b></p>
                    <ThumbUpIcon color='primary'></ThumbUpIcon>
                </div>
            </div>
            )
            }
            </div>

            <button style={styles.pt4_btn} onClick={() =>submit()}> Continue</button>
        </div>
    </>)
}

const MultipartForm = ({ step, setSteps, formData, setFormData }) => {
    switch (step) {
        case 0: return <Part1 formData={formData} setFormData={setFormData} setSteps={setSteps} />;
        case 1: return <Part2 formData={formData} setFormData={setFormData} setSteps={setSteps} />;
        case 2: return <Part3 formData={formData} setFormData={setFormData} setSteps={setSteps} />;
        case 3: return <Part4 formData={formData} setFormData={setFormData} setSteps={setSteps} />;
    }
}

export default MultipartForm;

