import axios from 'axios';

const url="/";
export const postCampaign=async (body)=>{
    return await axios.post(url+'campaign/post',body);
}

export const getCampaigns=async(filter)=>{
    return await axios.get(url+`campaign/all?title=${filter.title}&platform=${filter.platform}&status=${filter.status}&date=${filter.date}`);
}

export const getProducts=async()=>{
    return await axios.get(url+'product/all');
}

export const toggleLive=async(body)=>{
    return await axios.post(url+'campaign/toggleLive',body);
}

export const deleteCampaign=async(body)=>{
    return await axios.delete(url+`campaign/delete?id=${body._id}`);
}