import DataTable from 'react-data-table-component';
import React from 'react';
import Switch from '@mui/material/Switch';
import { getCampaigns,toggleLive,deleteCampaign} from '../api';
import EditIcon from '@mui/icons-material/Edit';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';

const styles = {
    col: { width: '200', height: 80, display: 'flex', justifyContent: 'center' ,alignItems:'center'}
}

const GetPlatform=(task)=>{
    switch(task){
        case 1:
        case 7:
        case 9: return <>"Google"</>;break;
        case 5:
        case 8: return <>"Youtube" </>;break;
        case 6: return <>"Instagram"</>;break;
        default :return <>"FaceBook"</>;break;
    }
}

const columns = (get_data)=>[
    {
        name: 'On/Off',
        selector: row => <Switch
            checked={row.onOff}
            onClick={()=>{toggleLive(row);get_data();}}
            inputProps={{ 'aria-label': 'controlled' }}
        />,
        sortable: true,
    },
    {
        name: 'Campaign',
        selector: row => <div style={styles.col}>
            <img style={{ height: 54,marginRight:5 }} src={row.product.icon}></img>
            <div>
            <p><b>{row.product.title}</b></p>
            <p style={{color:'gray'}}><b>Created at {new Date(row.startDate).toLocaleDateString()}</b></p>
            </div>
            </div>,
        grow: 2.3,
        sortable: true,
    },
    {
        name: 'DataRange',
        selector: row => <b>{new Date(row.startDate).toLocaleDateString()}-{new Date(row.endDate).toLocaleDateString()}</b>,
        grow:1.3,
        sortable: true,
    },
    {
        name: 'Clicks',
        selector: row => <b>{row.clicks}</b>,
        sortable: true,
    },
    {
        name: 'Budget',
        selector: row => <b>INR {row.budget}</b>,
        sortable: true,
    },
    {
        name: 'Location',
        selector: row => <b>{row.location}</b>,
        sortable: true,
    },
    {
        name: 'Platform',
        selector: row => <b>{GetPlatform(row.taskid)}</b>,
        sortable: true,
    },
    {
        name: 'Status',
        selector: row => <b>{row.onOff? <>Live</>:<>Not Live</>}</b>,
        sortable: true,
    },
    {
        name: 'Actions',
        selector: row => <div style={{display:'flex',justifyContent:'space-around',width:70}}>
            <EditIcon style={{cursor:'pointer',color:'blue'}} ></EditIcon>
            <DeleteForeverIcon style={{cursor:'pointer',color:'red'}} onClick={()=>{deleteCampaign(row);get_data();}}></DeleteForeverIcon>
        </div>,
        sortable: true,
    },
];

const paginationComponentOptions = {
    noRowsPerPage:true,
    rangeSeparatorText: 'de',
    selectAllRowsItem: true,
    selectAllRowsItemText: 'Todos',
    rowsPerPage:5
};

const CampaignTable = ({get_data,Data}) =>
{ 
    return(
    <DataTable 
        columns={columns(get_data)}
        data={Data}
        selectableRows
        pagination paginationComponentOptions={paginationComponentOptions}
    />
);
}


export default CampaignTable;
