import icon from '../public/Icon.png';
import campaign from '../public/campaign.png';
import home from '../public/house.png';
import customer from '../public/customer.png';
import products from '../public/bag.png';
import {Link} from 'react-router-dom';

const styles={
    sidebar:{
      flex:1,
      display:'flex',
      height:'100%',
      flexDirection:'column',
      backgroundColor:'rgba(0, 23, 56, 1)',
      alignItems:'center'
    },
    mainbar: {
        height: '100vh',
        width: '100%',
        flex: 15,
        flexDirection: 'column',
        backgroundColor: '#f6f9ff'
    },
    icon:{
        margin:30,
        width:46
    },
    btn:{
        display:'flex',
        paddingTop:10,
        flexDirection:'column',
        height:80,
        width:'100%',
        alignItems:'center',
        cursor:'pointer',
        borderLeft:'10px solid rgba(0, 23, 56, 1)',
        textDecoration:'none',

    },
    img:{
        align:'center',
        margin:5
    },
    btnName:{
        color:'white',
        fontWeight:100
    },
    activebtn:{
        textDecoration:'none',
        paddingTop:10,
        display:'flex',
        flexDirection:'column',
        height:80,
        width:'100%',
        alignItems:'center',
        cursor:'pointer',
        backgroundColor:'#02224b',
        borderLeft: '10px solid #2475df'
    }

}

const sideButtons=[
    {src:home,title:"Home"},
    {src:campaign,title:"Campaign"},
    {src:customer,title:"Customer"},
    {src:products,title:"Products"},
]

const sidebar=({curActive,setActive})=>{
    
    const handleClick=(e,index)=>{
        setActive(index);
    }

    return(<>
    <div style={styles.sidebar}>
        <img style={styles.icon} src={icon} alt="Icon"></img>
        {sideButtons.map((item,index)=>{
            return (<>
            {
            <Link to='/' style={curActive===index? styles.activebtn:styles.btn} onClick={(e)=>handleClick(e,index)}><img src={item.src} style={styles.img}></img>
            <p style={styles.btnName}>{item.title}</p>
            </Link>
            }
            
            </>)
        })}
    </div>

    </>)
}

export default sidebar;