import Campaign from '../components/campaign';
import user_info from '../public/User_Info.png';

const styles = {
    
    user_info: {
        width: '100%'
    },
    mainbar: {
        height: '100%',
        width: '100%',
        flex: 15,
        flexDirection: 'column',
        backgroundColor: '#f6f9ff'
    },
}

const Mainbar = ({curActive}) => {
    return (<>
        <div style={styles.mainbar} >
            <img style={styles.user_info} src={user_info}></img>
            {curActive == 1 ? <Campaign /> : <h1 align="center">Only Campaign Creation is Working Currently</h1>}
        </div>
    </>)
}

export default Mainbar;
