import EmojiObjectsIcon from '@mui/icons-material/EmojiObjectsRounded';
import ProductionQuantityLimitsIcon from '@mui/icons-material/ProductionQuantityLimits';
import AppSettingsAltIcon from '@mui/icons-material/AppSettingsAlt';
import CheckIcon from '@mui/icons-material/Check';
import {useState} from 'react';
import MultipartForm from './mainform';

const styles = {
    top: {
        display: 'flex',
        width: '100%',
        alignItems: 'center',
        justifyContent: 'space-between'
    },
    container: {
        margin: 25
    },
    progress: {
        width: '100%',
        display: 'flex',
        justifyContent: 'center',
        marginTop: 40,
        margin: 20
    },
    progressicon: { borderRadius: '50%', backgroundColor: 'orange', height: 50, width: 50, display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }
    ,
    low: { width: '40vh', display: 'flex', flexDirection: 'column', justifyContent: 'center' },
    high: {
        width: '40vh', display: 'flex', flexDirection: 'column', justifyContent: 'center',
        border: '1px solid orange'
    },
    titles: {
        width: '100%',
        display: 'flex',
        justifyContent: 'space-around',
        marginTop: 40,
        margin: 20
    }
}

const initialState={
    taskid:1,
    productid:1,
    timeline:0,
    startDate:'2022-09-21',
    endDate:'2022-09-25',
    budget:1,
    ltoggle:0,
    location:'India',
    radius:1,
    outlookid:1,
    product:{}

}


const FormContent = () => {
    const [step,setSteps]=useState(0);
    const [formData,setFormData]=useState(initialState);
    console.log(step);
    return (<>
        <div className="continer" style={styles.container}>
            <div style={styles.top}>
                <div>
                    <h1>Your Ad Campaign</h1>
                    <p style={{ color: 'gray' }}>Launch your ad in just 4 easy steps</p>
                </div>
            </div>
            <div style={styles.progress}>
                <div style={styles.progressicon}>
                    <EmojiObjectsIcon style={{ color: 'white' }} />
                </div>

                <div style={styles.low}><hr style={ step>0 ? styles.high:styles.low} /></div>

                <div style={styles.progressicon}>
                    <ProductionQuantityLimitsIcon style={{ color: 'white' }} />
                </div>

                <div style={styles.low}><hr style={ step>1 ? styles.high:styles.low} /></div>

                <div style={styles.progressicon}>
                    <AppSettingsAltIcon style={{ color: 'white' }} />
                </div>

                <div style={styles.low}><hr style={ step>2 ? styles.high:styles.low} /></div>

                <div style={styles.progressicon}>
                    <CheckIcon style={{ color: 'white' }} />
                </div>

            </div>

            <div style={styles.titles}>
                <p>What you want to Do</p>
                <p>Choose Product</p>
                <p>Campaign Setting</p>
                <p>Ready To Go</p>
            </div>
            {MultipartForm({step,setSteps,formData,setFormData})}
        </div>
    </>)
}

export default FormContent;
