import user_info from '../public/User_Info.png';
import FormContent from './FormContent';

const styles = {
    user_info: {
        width: '100%'
    },
    mainbar: {
        height: '100vh',
        width: '100%',
        flex: 15,
        flexDirection: 'column',
        backgroundColor: '#f6f9ff'
    },
}

const Form = () => {
    return (<>
        <div style={styles.mainbar} >
            <img style={styles.user_info} src={user_info}></img>
            <FormContent ></FormContent>
        </div>
    </>)
}

export default Form;
